.home {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.home .results {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.home .results .result {
  margin: 1rem;
  border: 1px solid #a2a2a2;
  border-radius: 10px;
  display: flex;
  text-align: center;
}

.home .results .result a {
  padding: 1rem;
  text-decoration: none;
  color: #000;
}

/* Detail */

.detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10%;
  text-align: center;
}

.detail > h1 {
  font-size: 4rem;
}
.note {
  display: flex;
  align-items: center;
  font-size: 2rem;
}
.detail > img {
  margin: 0 10%;
  width: -webkit-fill-available;
}
.summary {
  text-align: center;
}
.summary p {
  text-align: left;
}
.casting {
  text-align: center;
}
.persons {
  display: flex;
  flex-wrap: wrap;
}
.persons > * {
  margin: 1rem;
}

@media screen and (max-width: 800px) {
  .detail > img {
    margin: 10%;
  }
  .persons {
    justify-content: center;
  }
}
